.notes{
    padding: 25px;
    height: 280 px;
    width: 260px;
    display: flex;
    flex-direction: column;
    background: blue;
    border-radius: 30px;
}
.notes textarea{
    height: 30rem;
}
.notes .notesText{
    flex: 1;
    resize: none;
    background-color: transparent;
    font-size: 1rem;
    line-height: 1;
    outline: none;
    border: none;
}
.notes .notesFooter{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.notes .notesFooter img{
    height: 30px;
    cursor: pointer;
    opacity: 0.2;
    transition:0.3s all ease ;
}
.notes:hover .notesFooter img{
    opacity: 1;
}
