.notesHolder{
    height: 100%;
    width: 92%;
    margin: auto;
}
.notesHolderHeading {
    display: flex;
    align-items: center;
    border-radius: .3rem;
    justify-content: space-between;
}
.notesHolderHeading img{
    height: 30px;
    border-radius: .3rem;
    cursor: pointer;
    border: none;
}
.notesHolderHeading img:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.notesHolderSearch{
    display: flex;
    align-items: center;
}
.notesHolderSearch input{
    display: none;
    font-size: 1rem;
    outline: none;
    padding: .15rem;
    background-color: linear-gradient(45deg ,rgb(38, 119, 255),rgb(101, 84, 255));
    transition: 300ms;

}
.notesHolderSearch input.active{
    display: block;
}
.notesHolder h2{
    padding: .5rem;
}
.notesHolder .notesContainer{
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between;     */
    gap: 20px;
    height: 90%;
    padding: 1.5rem;
    overflow-y: scroll;
}

.notesContainer::-webkit-scrollbar{
    width: 2px;
}
.notesContainer::-webkit-scrollbar-track{
    background: transparent;
}
.notesContainer::-webkit-scrollbar-thumb{
    border-radius: 20px;
    background: lightgrey;
    transition: 300ms;
}
.notesContainer::-webkit-scrollbar-thumb:hover{
    background: grey;
    /* width: 4px; */
}