.plus {
    position: absolute;
    bottom: 1rem;
    left: 1rem;
    display: flex;
    flex-direction: column-reverse;
    gap: 20px;
}

.plus button {
    background: transparent;
    outline: none;
    border: none;
    cursor: pointer;
    border-radius: 50%;
}

.plus button img {
    height: 50px;
    width: 50px;
}

.plus .plusList {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    height: 0;
    transition: 300ms;
}

.plus .plusListActive {
    height: auto;
    background: rgb(244, 241, 241);
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-radius: 10rem;
}

.plus .plusList .plusListColor {
    border-radius: 50%;
    height: 20px;
    width: 20px;
    list-style-type: none;
    ;
}